.App {
  width: 100vw;
  height: 100vh;
  background: #fff;
  color: #212121;
  /* font-family: 'Open Sans', sans-serif; */

  font-family: inherit;
  display: grid;
  place-items: center;
}
body {
  margin: 0%;
  padding: 0%;
}

.joinChatContainer {
  width: 100%;
  min-height: 100%;
  background-color: #f0f2f5;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  position: absolute;
  opacity: 1;
  top: 0;
  bottom: 20px;
  z-index: 100;
}

.joinChatContainer h3 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: #555555;
}
.joinChatContainer input {
  width: 210px;
  height: 40px;
  margin: 7px;
  /* border: 2px solid #43a047; */
  /* /* border: 2px solid #555555;d9d9d9 */
  border: 2px solid #d9d9d9;
  border-radius: 5px;
  padding: 5px;
  font-size: 16px;
}

.joinChatContainer button {
  width: 210px;
  height: 50px;
  margin: 7px;
  border: none;
  border-radius: 5px;
  padding: 5px;
  font-size: 16px;
  /* background: #43a047; */
  background: #1890ff;
  color: #fff;
  cursor: pointer;
}

.joinChatContainer button:hover {
  /* background: #2e7d32; */
  background: #40a9ff;
  color: #fff;
}

.chat-window {
  /* width: 300px;
    height: 420px; */
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  /* overflow-y: scroll; */
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: #d9d9d9;
  z-index: 100;
  background-color: rgb(241, 236, 228);
}

.chat-window p {
  margin: 0;
}
.chat-window .chat-header {
  /* height: 100%; */
  border-radius: 6px;
  /* background: #263238; */
  position: relative;
  cursor: pointer;
}
.chat-header {
  min-height: 10%;
}
.chat-window .chat-header p {
  display: block;
  padding: 0 1em 0 2em;
  color: #fff;
  font-weight: 700;
  line-height: 45px;
  background-color: rgb(38, 38, 39);
}
.chat-header p {
  text-align: center;
}
.chat-body {
  min-height: 80%;
}
.chat-window .chat-body {
  /* height: calc(450px - (45px + 70px)); */

  /* height: 80%; */
  /* border: 1px solid #263238; */
  /* background: #fff; */

  background-color: rgb(241, 236, 228);
  position: relative;
}
.chat-window .chat-body .message-container {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;

  background-color: rgb(241, 236, 228);

  /* background-color: #f0f2f5; */
}

.chat-window .chat-body .message-container::-webkit-scrollbar {
  display: none;
}
.chat-window .chat-body .message {
  height: auto;
  padding: 10px;
  display: flex;
}

.chat-window .chat-body .message .message-content {
  width: auto;
  height: auto;
  min-height: 40px;
  max-width: 120px;
  background-color: #43a047;
  border-radius: 5px;
  color: white;
  display: flex;
  align-items: center;
  margin-right: 5px;
  margin-left: 5px;
  padding-right: 5px;
  padding-left: 5px;
  overflow-wrap: break-word;
  word-break: break-word;
}
.chat-window {
  z-index: 100;
}

#you {
  justify-content: flex-end;
}

#you .message-content {
  justify-content: flex-start;
  background-color: #555555;
}

#you .message-meta {
  justify-content: flex-end;
  margin-left: 5px;
}

#other {
  justify-content: flex-start;
}

#other .message-content {
  justify-content: flex-start;
  /* background-color: cornflowerblue;1890ff */

  background-color: #1890ff;
}

#other .message-meta {
  justify-content: flex-start;
  margin-right: 5px;
}

.message-meta #author {
  margin-left: 10px;
  font-weight: bold;
}

.chat-window {
  width: 100%;
  height: 100%;
}

.chat-window .chat-body .message .message-meta {
  display: flex;
  font-size: 12px;
}

.chat-window {
  /* height: 40px; */
  border: 1px solid #263238;
  border-top: none;
  display: flex;

  /* bottom: 0; */
}

.chat-footer {
  border: 0;
  border-top: none;
  display: flex;
}

.chat-window .chat-footer input {
  height: 100%;
  /* flex: 85%; */
  border: 0;
  padding: 0 0.7em;
  font-size: 1em;
  /* border-right: 1px dotted #607d8b; */
  outline: none;
  /* font-family: 'Open Sans', sans-serif; */
  font-family: inherit;
}

.chat-window .chat-footer button {
  border: 0;
  display: grid;
  place-items: center;
  cursor: pointer;
  flex: 15% 1;
  height: 100%;
  background: transparent;
  outline: none;
  font-size: 12px;
  color: lightgray;

  background-color: rgb(38, 38, 39);
}

.chat-window .chat-footer button:hover {
  /* color: #43a047; */
  color: #fff;
}

.chat-footer {
  min-height: 10%;
  max-height: 40px;
}
.hide {
  opacity: 0 !important;
}

.nav_menu {
  padding: 0 20px;
  border-bottom: solid 1px #e8e8e8;
  overflow-y: hidden;
  box-shadow: 0 0 30px #f3f1f1;
  background-color: #ffffff;

  position: absolute;
  z-index: 5;
  width: 100%;

  /* from typeform css */
  min-width: 0px;
  min-height: 87px;
  max-height: 87px;
  flex-shrink: 1;

  box-sizing: inherit;

  display: block;
  overflow-x: hidden;

  height: 87px;
}

.nav_menu_home {
  max-width: 20%;
  float: left;
}

.nav_menu_home a {
  display: inline-block;
  font-size: 1.5em;
  padding: 19px 20px;
}

.nav_menu_container {
  width: 100%;
}

.nav_menu_right {
  max-width: 20%;
  float: right;
  display: inline-block;
  font-size: 1.5em;
  padding: 19px 20px;
}

